.cardInfos {
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        letter-spacing: 5px;
        text-transform: uppercase;
        font-size: 4rem;
        color: rgb(238, 207, 32);
        text-shadow: #0004fd 2px 2px, #0004fd -2px 2px, #0004fd -2px -2px, #0004fd 2px -2px;
    }

    .infos {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: lightcoral;
        padding: 20px;

        @media (max-width: 500px) {
            margin-top: 30%;
        }

        span {
            font-size: larger;
            color: #f1f1f1;
        }

        img {
            width: 70%;
            height: auto;
        }
    }
}